   License: none (public domain)
*/
* {
  box-sizing: border-box;
}

/* @import "../ReactParallaxTilt.scss"; */
/* ==================================== */

@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1300px !important;
  }
}

.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
  background: none !important;
  color: aqua !important;
}
.nav-tabs .nav-link {
  border: none !important;
}

.nav-item {
  font-size: 1.5rem;
}
.MuiButton-root {
  color: white !important;
}
.MuiInput-underline::before {
  border-bottom: 3px solid aliceblue !important;
}
.MuiInput-underline::after {
  border-bottom: 3px solid aquamarine !important;
}
.MuiButton-outlined {
  border: 1px solid rgba(255, 255, 255, 1) !important;
}

.fixed{position:fixed;top:0;width:100vw;z-index:100;background-color:#2e2d4f;padding-top:0.5rem;padding-bottom:0.5rem}.logo{font-size:2rem;font-weight:bold;color:aquamarine !important}.nav{font-size:1.3rem;color:white}.nav-item{color:white !important}

