re-scale .inner-element{margin-left:1rem;overflow:hidden;display:flex;flex-direction:column;justify-content:end;font-style:italic;color:white;transform:translateZ(60px)}.skills-section{background-color:#4e2990;padding-top:3rem;padding-bottom:10rem}.skills{display:flex;flex-direction:column;justify-content:center;align-items:center}.skills img{height:4rem;width:4rem;margin-bottom:1rem}.skills h4{color:white}

.footer{background-image:url(/static/media/footer-bg.aca525cd.png);background-repeat:no-repeat;background-size:cover;object-fit:cover;width:100% !important}.footer-info{color:white;list-style:none}.footer-info li{font-size:1.5rem}

